$yellow: #FAD186;
$orange: #FF9C85;
$purple: #B498FF;
$pink: #EDC3F0;
$teal: #9BCFD0;

html {
  font-family: 'VAG', 'Arial Rounded MT Bold', sans-serif;
}

#instructions {
  z-index: 8;
}

#game {
  z-index: 9;

}

#thanks {
  z-index: 10;
}

#instructions, #thanks, #game, #splash {
  position: absolute;
  top: 5%;
  left: 5%;
  width: 90%;
  height: calc(100% - 40px);

  .responsive-img {
    width: 100%;
  }

  .game-content {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }

  .game-bg {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

#thanks {
  height: 90%;

  .thanks-content {
    height: 70%;
    justify-content: space-around;
    display: flex;
    align-items: center;
    flex-direction: column;
    text-align: center;
  }

  .header {
    margin-top: -8vw;
    margin-bottom: -8vw;
  }

  .logo {
    position: absolute;
    bottom: -5%;
    right: -5%;
    width: 50%;
  }
}

.no-pointer-events {
  pointer-events: none;
}

.container {
  position: relative;
  margin: 0 auto;
  padding-top: 121%;

  background-size: cover;
  overflow:hidden;
}

button {
  padding: 12px 20px;
  border: none;
  background-color: $purple;
  font-size: 3vw;
  border-radius: 4px;
  cursor: pointer;
  font-family: 'VAG', 'Arial Rounded MT Bold', sans-serif;
}

.header {
  margin-top: -5vw;
  margin-bottom: -3vw;
}

.bodytext {
  font-size: 4vw;
  color: white;

  p {
    margin: 0;
  }
}
